	module.exports = {"data":{"markdownRemark":{"html":"<h2>I Tag</h2>\n<p>The <code>&#x3C;i></code> element is used to denote text that is set apart from its surrounding text in some way. By default, text surrounded by <code>&#x3C;i></code> tags will be displayed in italic type. </p>\n<p>In previous HTML specifications, the <code>&#x3C;i></code> tag was solely used to denote text to be italicized. In modern semantic HTML, however, tags such as <code>&#x3C;em></code> and <code>&#x3C;strong></code> should be used where appropriate. You can use the “class” attribute of the <code>&#x3C;i></code> element to state why the text in the tags is different from the surrounding text. You may want to show that the text or phrase is from a different language, as in the example below.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>The French phrase <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>i</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>french<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>esprit de corps<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>i</span><span class=\"token punctuation\">></span></span> is often \nused to describe a feeling of group cohesion and fellowship.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Element/i\">MDN</a></p>","fields":{"slug":"/html/elements/i-tag/"},"frontmatter":{"title":"I Tag"}}},"pathContext":{"slug":"/html/elements/i-tag/"}}

